@import url(https://fonts.googleapis.com/css2?family=Encode+Sans+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@200&family=Prompt:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
    background-color: white !important;
    box-shadow: 3px 5px 10px rgb(198, 196, 196) !important;
}
.nav-link {
    color: black !important;
    font-size: 1.2em;
    font-family: 'Prompt', sans-serif;
    font-weight: bolder;
}
.navimage{
    width: auto;
    height: 40px;
}
body{
    background-color: #ebeaea !important;
}
.signup-form{
    border: 1px solid none;
    padding-bottom: 3%;
    padding-top: 2%;
    border-radius: 6px;
    background-color: white !important;
    box-shadow: 3px 5px 10px rgb(198, 196, 196);
}
.signupimg{
    width: 50%;
}
input{
    width: 70% !important;
    margin: auto;
    padding-top: 2% !important;
    padding-bottom: 2% !important;
}
input::-webkit-input-placeholder{
    font-family: 'Quicksand', sans-serif;
    font-weight: 900;   
}
input:-ms-input-placeholder{
    font-family: 'Quicksand', sans-serif;
    font-weight: 900;   
}
input::placeholder{
    font-family: 'Quicksand', sans-serif;
    font-weight: 900;   
}
.blog-card{
    border-radius: 6px;
    box-shadow: 3px 5px 10px rgb(198, 196, 196);
    text-align: left;
}
.detail-card{
    box-shadow: 3px 5px 10px rgb(198, 196, 196);
    border-radius: 6px;
}
.blog-card-body{
    padding-right: 2%;
    padding-left: 7%;
}
.blog-title{
    font-size: 1.6em;
    color: black;
}
.create-blog-sec{
    border: 1px solid none;
    padding-bottom: 3%;
    padding-top: 2%;
    border-radius: 6px;
    background-color: white !important;
    box-shadow: 3px 5px 10px rgb(198, 196, 196);
}
.detail-card > .card-body > p {
    font-family: 'PT Sans', sans-serif !important;
    /* font-family: 'Montserrat Alternates', sans-serif !important; */
    /* font-weight: bold; */
}
